<template>
	<view class="page bgwhite">
		<view v-if="!isGet">
			<view class="list" :prop="detailData" v-if="detailData">
				<view class="list-item ch-flex">
					<view class="label">{{ $t('班次') }}：</view>
					<view class="ch-flex-1 value">{{ detailData.bcName }}</view>
				</view>
				<view class="list-item ch-flex">
					<view class="label">{{ $t('机器编号') }}：</view>
					<view class="ch-flex-1 value"> {{ detailData.machineNo }}</view>
				</view>
				<view class="list-item ch-flex">
					<view class="label">{{ $t('工单编号') }}：</view>
					<view class="ch-flex-1 value"> {{ detailData.mo }}</view>
				</view>
				<view class="list-item ch-flex">
					<view class="label">{{ $t('派工单号') }}：</view>
					<view class="ch-flex-1 value"> {{ detailData.dispatchNo }}</view>
				</view>
				<view class="list-item ch-flex">
					<view class="label">{{ $t('产品编号') }}：</view>
					<view class="ch-flex-1 value"> {{ detailData.itemCode }}</view>
				</view>
				<view class="list-item ch-flex">
					<view class="label">{{ $t('产品名称') }}：</view>
					<view class="ch-flex-1 value"> {{ detailData.itemName }}</view>
				</view>
				<view class="list-item ch-flex">
					<view class="label">{{ $t('日期') }}：</view>
					<view class="ch-flex-1 value"> {{ detailData.date }}</view>
				</view>
				<view class="list-item ch-flex">
					<view class="label">{{ $t('生产数') }}：</view>
					<view class="ch-flex-1 value"> {{ detailData.prodQty }}</view>
				</view>
				<view class="list-item ch-flex">
					<view class="label">{{ $t('良品数') }}：</view>
					<view class="ch-flex-1 value"> {{ detailData.goodQty }}</view>
				</view>
				<view class="list-item ch-flex">
					<view class="label">{{ $t('次品数') }}：</view>
					<view class="ch-flex-1 value"> {{ detailData.badQty }}</view>
				</view>
				<view class="list-item ch-flex">
					<view class="label">{{ $t('生产时间') }}(h)：</view>
					<view class="ch-flex-1 value"> {{ detailData.produceDate }}</view>
				</view>
				<view class="list-item ch-flex">
					<view class="label">{{ $t('试模时间') }}(h)：</view>
					<view class="ch-flex-1 value"> {{ detailData.testModeDate }}</view>
				</view>
				<view class="list-item ch-flex">
					<view class="label">{{ $t('停机时间') }}(h)：</view>
					<view class="ch-flex-1 value"> {{ detailData.stopDate }}</view>
				</view>
				<view class="list-item ch-flex">
					<view class="label auto">{{ $t('未刷卡停机时间') }}(h)：</view>
					<view class="ch-flex-1 value"> {{ detailData.noCardDate }}</view>
				</view>
				<view class="list-item ch-flex">
					<view class="label">{{ $t('次品率') }}(h)：</view>
					<view class="ch-flex-1 value"> {{ detailData.badRate }}%</view>
				</view>
			</view>
		</view>
		<template v-else>
			<u-loading-icon text="loading"></u-loading-icon>
		</template>
	</view>
</template>

<script>
import Api from "@/api/daily/index.js"
export default {
	data() {
		return {
			isGet: true,
			detailData: {},
		};
	},
	onShow() {
		uni.setNavigationBarTitle({ // 修改头部标题
			title: this.$t("day.report.detail")
		});
	},
	onLoad(options) {
		uni.$u.http.get(Api.detail + "dispatchId=" + options.dispatchId).then(result => {
			console.log('result', result);
			if (result.code === 200) {
				if (result.data != null) {
					this.detailData = result.data
				}
			}
		}).catch(er => {
			console.log('er', er);
		}).finally(() => {
			this.isGet = false
		})
	},
}
</script>

<style lang="scss" scoped>
.list {
	&-item {
		padding: 20upx 24upx;
		@include after();
		font-size: 28upx;

		.label {
			width: auto;
			color: #333;
			font-weight: 600;

			&.auto {
				width: auto
			}
		}

		.value {
			text-align: right;
			color: #666;
		}
	}
}
</style>